

















































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import Page from '@/components/common-components/page.vue'
import pageView from '@/components/common-ui/page-view.vue'
import navView from '@/components/nav/index.vue'
@Component({
  components: {
    Page,
    pageView,
    navView
  }
})
export default class Home extends Vue {
  $route: any
  $createToast : any
  $createDialog: any
  point = {}
  // 声明周期钩子
  fleetId : any
  title: any
  showMore = true
  currentTab = ''
  dataList = []

  timeRows = []
  created () {
    this.fleetId = this.$route.query.fleetId
    this.title = '报名任务'
    this.getFleetInfoById(this.fleetId)
  }

  get showTimes () {
    return this.timeRows && this.timeRows.length > 0
  }
  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  // get showMore () {
  //   return Math.ceil(this.count / this.limit) >= (this.page + 1)
  // }
  getFleetInfoById (id : any) {
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getFleetInfoById', { id: id }).then((response) => {
      if (response.success) {
        let data = response.data;
        let fleetTask = data.fleetTask || []
        this.dataList = fleetTask;
        this.$set(this, 'timeRows', data.timeRows)
      }  
      toast.hide()
    })
  }

  

  // addTask () {
  //   this.$router.push({ name: 'mapSelect', query: {} })
  // }

  dialog = null;
  getTime(index){
    if (!this.showTimes) {
      return ''
    }

    let currentIndex = this.currentTab === 'start' ? index : this.pointObj.start.length + index;
    if (!this.timeRows[currentIndex]) {
      return ''
    }
    let cObj = this.timeRows[currentIndex][currentIndex + 1]
    if (cObj) {
      return '去下个点位  预估路程：' + Math.floor(cObj.distance/1000) + '公里  ' + '预估时间：' + this.secondsToTime(cObj.duration)
    } else {
      return '此点为终点站~'
    }
  }
  addTask () {
    this.$router.push({ name: 'fleetTaskUpdate', query: {
      type: 'add',
      data: {}
    } })
  }

  secondsToTime(seconds) {
    var hours, minutes, remainingSeconds;
    hours = Math.floor(seconds / 3600);
    remainingSeconds = seconds % 3600;
    minutes = Math.floor(remainingSeconds / 60);
    remainingSeconds = remainingSeconds % 60;
    let text = ''
    if (hours) {
      text = text + hours + '小时'
    }
    if (minutes) {
      text = text + minutes + '分钟'
    }
    if (remainingSeconds) {
      text = text + remainingSeconds + '秒'
    }
    return text
}
  saveTask () {
    const toast = this['$createToast']({
      time: 0,
      txt: '保存中...'
    })
    toast.show()
    this.$store.dispatch('updateFleetInfoById', { id: this.fleetId, key: 'fleetTask', data: this.dataList }).then((response) => {
      if (response.success) {
        console.log(response, 'oooooooooo')
        this.$createDialog({
          type: 'alert',
          title: '提示',
          content: '保存成功！',
          icon: 'cubeic-alert'
        }).show()
      }  
      toast.hide()
    })
  }

  validate () {
    let state = true;
    let message = ''
    Object.keys(this.pointObj).forEach((key)=>{
      if (this.pointObj[key].length === 0) {
        state = false
        message = key === 'start' ? '出发点位必填' : '到达点位必填'
      } else {
        this.pointObj[key].forEach((item) => {
          if (!item.point || !item.point.location) {
            state = false
            message = item.label + '未添加定位！'
          } 
        })
      }
    })

    if (!state) {
      this.$createDialog({
        type: 'alert',
        title: '提示',
        content: message,
        icon: 'cubeic-alert'
      }).show()
    }
    return state;
  }
  addOrUpdatePoint (index) {
    let currentPoint = this.pointObj[this.currentTab][index]
    let currentType = currentPoint.point ? 'update' : 'add'
    let currentTabType = this.currentTab;
    this.$router.push({ name: 'mapSelect', query: {
      backInfo: {
        updateIndex: index,
        updateType: currentTabType 
      },
      location: currentType === 'update'? currentPoint.point.location : []
    } })
  }
  deletePoint (index: any) {
    let content = '任务删除将不会恢复，请确认是否删除！'
    let dialog = this.$createDialog({
      type: 'confirm',
      title: '删除确认',
      content: content,
      confirmBtn: {
        text: '确认删除',
        active: true,
        disabled: false,
        href: 'javascript:;'
      },
      cancelBtn: {
        text: '取消',
        active: false,
        disabled: false,
        href: 'javascript:;'
      },
      onConfirm: () => {
        this.dataList.splice(index, 1)
      },
      onCancel: () => { }
    })
    dialog.show()
  }

  updateDialotText (value, index) {
    this.pointTextDialot(value, 'update', index)
  }

  pointTextDialot (value, type, index) {
    this.$router.push({

    })
  }
  navChange (value) {
    console.log(value)
    this.currentTab = value
  }

  showInfo (info) {
    let departureTimeList = []
    if (info.taskType === 'time') {
      departureTimeList = info.timeValue.map((item) => {
        return new Date(item).getTime()
      });
    }
    this.$router.push({ name: 'fleetTaskInfo', query: {
        fleetId: this.fleetId,
        departureTimeList: departureTimeList
      }
    })
  }

  @Watch('$route')
    routeChange (newVal) {
    let backData = this.$store.state.backData;
    if (backData.route === 'fleetTask') {
      let info = backData.data
      if (info.type === 'add') {
        console.log(info.data)
        this.dataList.push(info.data)
      }
      this.$store.commit('SET_BACK_DATA', {})
    }
   }

}
